import React from 'react';
import Page from 'components/Page';
import List, { ListItem } from 'components/List';
import AppVersion from 'components/AppVersion';
import WithFooter from 'components/WithFooter';

const ConsumerAccount = ({ history }) => {
  const handle = localStorage.getItem('form-value-handle') || 'spotravnyy';
  const email =
    localStorage.getItem('form-value-email') || 'spotravnyy@lohika.com';

  return (
    <Page
      className="topLevelPage"
      title="Account Settings"
      onBackNavigation={() =>
        // eslint-disable-next-line no-alert
        window.alert('This action will close the webview')
      }
    >
      <WithFooter footer={<AppVersion />}>
        <List>
          <ListItem
            title="Username"
            onClick={() => history.push('/consumer/account/handle')}
          >
            {handle}
          </ListItem>

          <ListItem
            title="Email"
            onClick={() => history.push('/consumer/account/email')}
          >
            {email}
          </ListItem>

          <ListItem onClick={() => history.push('/consumer/account/password')}>
            Update Password
          </ListItem>
        </List>
      </WithFooter>
    </Page>
  );
};

export default ConsumerAccount;
